html {}
body {
  font-family: 'Eina03', sans-serif;
  font-size: 12px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1165px;
  box-sizing: border-box;
  @include r(1250) {
    padding: 0 15px; } }
a {
  text-decoration: none;
  transition: all 0.3s; }
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none; }
textarea {
  resize: none; }


@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }




.selectize-input {
  border: none;
  border-bottom: 1px solid rgba($very-light-pink-five, 0.5);
  box-shadow: none !important;
  border-radius: 0;
  padding: 4px 0;
  width: 195px;
  margin-top: 5px; }
.selectize-input>input {
  display: none !important; }
.selectize-input>* {
  font-family: 'Eina03', sans-serif;
  color: $charcoal-grey;
  font-size: 14px; }
.selectize-input:after {
  right: 2px !important; }
.selectize-dropdown [data-selectable] {
  color: $white; }
.selectize-dropdown {
  border-radius: 3px;
  background-color: $charcoal-grey;
  box-shadow: none;
  .active {
    background: $charcoal-grey-two; } }


.btn {
  padding: 10px 15px;
  border-radius: 19px;
  font-size: 12px;
  &-primary {
    border: 1px solid $light-blue-grey;
    padding: 10px 22px;
    color: $charcoal-grey;
    &:hover {
      background: $light-blue-grey;
      color: $white; } }
  &-secondary {
    color: $white;
    background-color: $mango;
    font-weight: bold;
    border: 1px solid $mango;
    &:hover {
      border-color: $mango;
      color: $mango;
      background: none; } } }







.aside {
  width: 70px;
  height: 100vh;
  box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: $charcoal-grey;
  @include center-column;
  justify-content: flex-start;
  padding: 20px 0;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  @include r(767) {
    width: 45px; }
  img {
    width: 30px;
    @include r(767) {
      width: 22px; } }
  &__logo {
    padding-bottom: 40px; }
  &__menu {
    @include center-column;
    li {
      position: relative;
      padding: 15px 20px;
      @include tr;
      @include r(767) {
        padding: 15px 10px; }
      &:before {
        content: '';
        position: absolute;
        width: 30px;
        height: 100%;
        right: -30px;
        top: 0; }
      &:hover {
        background: rgba(#000, 0.2);
        ul {
          visibility: visible;
          opacity: 1; }
        i {
          color: $white; } }
      ul {
        visibility: hidden;
        opacity: 0;
        @include tr;
        position: absolute;
        left: 80px;
        top: 10px;
        border-radius: 4px;
        border: solid 0 $black;
        background-color: $charcoal-grey;
        padding: 6px 0;
        @include r(767) {
          left: 60px; }
        &:before {
          content: '';
          border: 10px solid transparent;
          border-right: 10px solid $charcoal-grey;
          position: absolute;
          left: -17px;
          top: 9px; }
        li {
          padding: 0;
          &:before {
            display: none; } }
        a {
          padding: 6px 20px;
          font-size: 12px;
          font-weight: 600;
          color: $pale-grey;
          &:hover {
            background: $charcoal-grey-two; } } } }

    a {
      display: flex;
      align-items: center;
      position: relative;
      i {
        position: absolute;
        right: -17px;
        color: $blue-grey;
        font-size: 14px;
        @include tr;
        @include r(767) {
          right: -14px; } } } } }


.breadcrumbs {
  display: flex;
  align-items: center;
  a {
    font-size: 12px;
    font-weight: 600;
    color: $brownish-orange;
    &:after {
      content: '/';
      margin: 0 5px;
      color: $dark; }
    &:last-child {
      color: $dark; } } }








.pages {
  padding-top: 60px;
  h1 {
    color: $mango;
    font-size: 30px;
    margin-bottom: 30px;
    text-align: center; }
  ul {
    @include center-column;
    a {
      color: rgba(52, 53, 54, 0.6);
      padding: 5px 0;
      font-size: 18px;
      display: block;
      &:hover {
        color: $leafy-green; } } } }
